.status-area {
  grid-area: status;
  display: flex;
  flex-direction: column;
  background: #455072;
  height: 500px;
  width: 14.3rem;
  border: 1px solid black;

  .current-turn {
    height: 30px;
    align-self: center;
    justify-content: center;

  }

  .status-messages {
    background: #455072;
    border-top-color: 1px solid black;
    height: 470px;
    width: 14rem;
    overflow-y: scroll;
    overflow-x: hidden;
    flex-grow: 1;


    .status-name {
      //background: #2d52c0;
      width: 70px;
      border-radius: 10px;
      padding-left: 2%
    }

    .status-msg {
      padding-left: 8%
    }

  }

}