body {
  background-color: #15232D;
  color: bisque;
  font-family: Arial, Helvetica, sans-serif;
  min-width: 1340px; }

.navbar {
  grid-area: nav;
  background: #455072; }

.play-area {
  display: grid;
  grid-gap: 2px;
  grid-template-columns: 1fr 510px 510px 14rem 1fr;
  grid-template-rows: 1rem 1fr 50px 300px;
  grid-template-areas: "nav nav nav nav nav" "stats enemy player status ." "stats chat chat chat .";
  align-content: center; }

.chat-area {
  grid-area: chat; }

.canvas-enemy {
  grid-area: enemy;
  height: 510px;
  width: 510px; }

.canvas-player {
  grid-area: player;
  height: 510px;
  width: 510px; }

.reset {
  grid-area: reset;
  border: 3px solid #01f351; }

.chart-test {
  grid-area: stats;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid #00000091;
  background: #455072; }

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.status-area {
  grid-area: status;
  display: flex;
  flex-direction: column;
  background: #455072;
  height: 500px;
  width: 14.3rem;
  border: 1px solid black; }
  .status-area .current-turn {
    height: 30px;
    align-self: center;
    justify-content: center; }
  .status-area .status-messages {
    background: #455072;
    border-top-color: 1px solid black;
    height: 470px;
    width: 14rem;
    overflow-y: scroll;
    overflow-x: hidden;
    flex-grow: 1; }
    .status-area .status-messages .status-name {
      width: 70px;
      border-radius: 10px;
      padding-left: 2%; }
    .status-area .status-messages .status-msg {
      padding-left: 8%; }

.chat-area {
  display: grid;
  height: 300px;
  grid-template-columns: 1fr;
  grid-template-rows: 30px 1fr 30px;
  grid-template-areas: "title" "messages" "input"; }
  .chat-area .chat-title {
    grid-area: title;
    text-align: center;
    margin: 0;
    border: 1px solid #101011;
    background-color: #455072; }
  .chat-area .chat-input {
    grid-area: input;
    position: relative;
    overflow: hidden;
    padding: 0 70px;
    flex-shrink: 0;
    border-bottom: 1px solid #101011;
    border-left: 1px solid #101011;
    border-right: 1px solid #101011;
    background-color: #1F4360; }
    .chat-area .chat-input input[type="text"] {
      width: 100%;
      margin-left: -20px;
      margin-right: -20px; }
  .chat-area .messages {
    grid-area: messages;
    overflow-y: scroll;
    overflow-x: hidden;
    flex-grow: 1;
    padding: 20px;
    background: #1F4360;
    border-left: 1px solid #101011;
    border-right: 1px solid #101011; }
    .chat-area .messages .username {
      font-weight: bold;
      font-size: 1rem;
      color: #999;
      margin-bottom: 5px; }
    .chat-area .messages .message-body {
      max-width: 80%;
      display: inline-block;
      padding: 3px;
      margin-left: 1%;
      background-color: #792e2e;
      border: 1px;
      border-radius: 10px;
      padding-right: 30px; }
    .chat-area .messages .enemy .message-body {
      background-color: #8f282f;
      margin-bottom: 5px; }
    .chat-area .messages .player-message .message-body {
      background-color: #464478;
      margin-bottom: 5px;
      color: white; }

.player-details {
  display: flex;
  flex-direction: column;
  width: 30rem;
  margin-top: 10%;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 8px 0 rgba(117, 117, 117, 0.212), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border: 1px solid #000000;
  background-color: #1F4360;
  border-radius: 2%; }
  .player-details .player-h {
    align-self: center; }
  .player-details .room-id {
    margin-top: 8%; }
  .player-details input {
    width: 90%;
    height: 100%; }
  .player-details button {
    width: auto;
    background-color: #ddeefc;
    border-color: #7f8b96;
    color: black;
    margin: 11px; }
  .player-details button:hover {
    background-color: #436988;
    border-color: #436988;
    color: #f7ffe2; }

